import React from 'react';
import { color, shadow } from '../constants';
import styled  from 'styled-components';

const Button = styled.button`
  outline: none !important;
  border: none;
  background: ${color.prim};
  padding: .5rem 1.5rem;
  border-radius: 2rem;
  color: ${color.light};
  transition: 250ms ease;
  text-transform: uppercase;
  font-weight: bold;
  box-shadow: ${shadow.sm};
  &:hover{
    background: ${color.primLight};
  }
  &:active{
    background: ${color.primDark};
  }
`

export default ({ children, onClick, buttonType, id, className, sm, md, lg, outline, style, dark, light, onMouseOver, onMouseLeave })=> (
  <Button
    type={buttonType}
    onMouseOver={onMouseOver}
    onMouseLeave={onMouseLeave}
    id={id}
    className={className}
    style={style}
    sm={sm}
    md={md}
    lg={lg}
    outline={outline}
    dark={dark}
    light={light}
    onClick={onClick}
  >
    {children}
  </Button>
)
