import React, { useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import { color } from '../constants';
import styled from 'styled-components';
import { gsap } from 'gsap';
import { useMedia } from '../hooks';

const Modal = styled.div`
  position: fixed;
  top: -50%;
  right: -50%;
  width: 0;
  height: 0;
  background-color: ${color.prim};
  transform-origin: top right;
  border-radius: 50%;
  z-index: 110;
`
export default () => {
  const visible = useSelector(state => state.menu);
  let modalRef = useRef(null);
  let main = useRef(null);
  const movil = useMedia('xs');
  const unit = movil ? 'vh' : 'vw';

  useEffect(()=>{
    if(visible){
      //gsap.set(main, { width: '100%', height: '100%'  });
      gsap.to(modalRef, { width: `200${unit}`, height: `200${unit}` });
    } else{
      gsap.to(modalRef, { width: 0, height: 0 });
      //gsap.set(main, { width: 0, height: 0 });
    }
  },[visible])

  //return <MainCont ref={el => { main = el }}><Modal ref={el => { modalRef = el }} /></MainCont>
  return <Modal ref={el => { modalRef = el }} />
}
