import React, { useEffect } from 'react';
import { color, font, lorem } from '../constants';
import styled from 'styled-components';
import { gsap } from 'gsap';
import Title from './title';
import { Container, Row, Col } from 'react-bootstrap';
import aboutImg from '../images/backgrounds/about.jpg';
import Button from './button';
import { useObserver } from 'raos';
import { BrandIcons } from '../static-info';


const AboutCont = styled.section`
  position: relative;
  padding: 4.5rem 0;
  position: relative;
  user-select: none;
  color: ${color.light};
  overflow: hidden;
  @media (min-width: 576px){
    //height: 100vh;
  }
`
const ParticlesCont = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
`
const SectionCont = styled.div`
  //height: 40vh;
  position: relative;
  z-index: 6;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  @media(min-width: 576px) {
    height: 100%;
    width: ${({responsive}) => responsive ? responsive : '100%'};
  }
`
const AboutImage = styled.div`
  width: 100%;
  height: 100%;
  min-height: 40vh;
  background-image: url(${aboutImg});
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
`
const TintImg = styled.div`
  background-color: ${color.prim};
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: .5;
  transition: 250ms ease;
  ${AboutImage}:hover &{
    opacity: 0;
  }
`
const ButtonCont = styled.div`
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  z-index: 5;
  transition: 250ms ease;
  opacity: 1;
`
const InfoCont = styled.div`
  border: 10px solid ${color.prim};
  border-right: none;
  padding: 1rem;
  font-size: 1rem;
  //height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  //align-items: center;
  overflow: hidden;
  @media(min-width: 576px){
    padding: 2rem;
  }
`
const TitleInfo = styled.h3`
  width: 100%;
  @media(min-width: 576px){
    width: 50%;
  }
`
const LoopCont = styled.div`
  position: relative;
  margin-top: .5rem;
  height: 10vh;
  width: calc(100% + 240px);
  left: -120px;
  z-index: 5;
  bottom: 0;
  overflow: hidden;
`

export default ()=> {
  let [image, setImage] = useObserver();
  let [info, setInfo] = useObserver();
  useEffect(()=> {
    const width = window.innerWidth;
    gsap.set('.loopItem', {
      position: 'absolute',
      x: index => (index * 60) + width,
    })
    gsap.to('.loopItem', 120, {
      ease: 'none',
      x: `-=${(BrandIcons.length * 60)}`,
      modifiers: {
        x: gsap.utils.unitize(x => {
          return parseFloat(x) % (BrandIcons.length * 60);
        })
      },
      repeat: -1,
    })
  },[])
  return(
    <AboutCont id="about">
      <Container>
        <Row>
          <Col xs={12}><Title>nosotros</Title></Col>
          <Col xs={12} sm={{ span: 6, order: 12 }}>
            <SectionCont responsive="70vw" ref={el => setImage(el)} className={image ? 'animated fadeInRight' : null}>
                <AboutImage>
                  <TintImg /><TintImg />
                  <ButtonCont>
                    <Button>contacto</Button>
                  </ButtonCont>
                </AboutImage>
            </SectionCont>
          </Col>
          <Col xs={12} sm={6}>
            <SectionCont ref={el => setInfo(el)} className={info ? 'animated fadeIn' : null}>
              <InfoCont>
                <TitleInfo>Tineo Web Design</TitleInfo>
                <p>{lorem.xl}</p>
                <p>{lorem.lg}</p>
                <LoopCont>
                  {
                    BrandIcons.map(Icon => <Icon className="loopItem" style={{ fill: color.light }} width={50} height={50} />)
                  }
                </LoopCont>
              </InfoCont>
            </SectionCont>
          </Col>
        </Row>
      </Container>
    </AboutCont>
  )
}
