import React, { useEffect } from 'react';
import { color, font, lorem } from '../constants';
import styled, { keyframes } from 'styled-components';
import Title from './title';
import { Container, Row, Col } from 'react-bootstrap';
import mars from '../images/backgrounds/mars.png';
import { gsap } from 'gsap';
import { ReactComponent as UXIcon } from '../images/iconos/mic/pen-fancy.svg';
import { ReactComponent as CodeIcon } from '../images/iconos/mic/laptop-code.svg';
import { ReactComponent as ServerIcon } from '../images/iconos/mic/server.svg';
import { useObserver } from 'raos';


const ServicesCont = styled.section`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  min-height: 100vh;
  padding: 4.5rem 0;
  position: relative;
  user-select: none;
  color: ${color.dark};
  @media (min-width: 576px){
    flex-direction: row;
  }
`
const rotate = keyframes`
  to{
    transform: rotate(360deg);
  }
`
const Mars = styled.img`
  width: 50%;
  position: absolute;
  top: -10%;
  left: 0;
  animation: ${rotate} 120s linear infinite;
  z-index: 5;
`

const ServiceCont = styled.div`
  display: flex;
  flex-direction: row;
  background-color: ${color.prim};
  background-image: linear-gradient(to right bottom, ${color.primLight}, ${color.primDark});
  border-radius: .5rem;
  margin-bottom: 2rem;
  animation-delay: 500ms;
  opacity: 0;
  @media(min-width: 576px){
    margin: 0;
    animation-delay: ${({ delay }) => delay};
  }
`
const ServiceIcon = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
`
const ServiceInfo = styled.div`
  padding: .5rem;
`
const Service = ({ Icon, children, delay })=> {
  const [observe, setObserve] = useObserver(0);
  return(
    <ServiceCont delay={delay} className={observe ? 'animated fadeIn' : null} ref={el => setObserve(el)}>
      <ServiceIcon>
        <Icon width={80} height={80} fill={color.dark} />
      </ServiceIcon>
      <ServiceInfo>
        {children}
      </ServiceInfo>
    </ServiceCont>
  )
}
export default ()=> {
  return(
    <ServicesCont id="service">
      <Mars src={mars} alt="mars planet" />
      <Container style={{zIndex: "6", position: "relative"}}>
        <Row>
          <Col xs={12}>
            <Title>Servicios</Title>
          </Col>
          <Col xs={12} sm={4}>
            <Service delay="500ms" Icon={UXIcon}>
              <h3>UX/UI</h3>
              <p>{lorem.lg}</p>
            </Service>
          </Col>
          <Col xs={12} sm={4}>
            <Service delay="1000ms" Icon={CodeIcon}>
              <h3>Frontend</h3>
              <p>{lorem.lg}</p>
            </Service>
          </Col>
          <Col xs={12} sm={4}>
            <Service delay="1500ms" Icon={ServerIcon}>
              <h3>Backend</h3>
              <p>{lorem.lg}</p>
            </Service>
          </Col>
        </Row>
      </Container>
    </ServicesCont>
  )
}
