import React, { useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { color } from '../constants';
import styled, { keyframes } from 'styled-components';
import { ON_MENU } from '../store/actions';
import { gsap } from 'gsap';

const fadeIn = keyframes`
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
`

const Button = styled.button`
  position: relative;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  height: 25px;
  width: 40px;
  padding: 0;
  outline: none !important;
  opacity: 0;
`
const Line = styled.div`
  background-color: ${({ menu, down }) => menu || down ? `${color.dark} !important` : color.light};
  height: 3px !important;
  width: ${({ mid }) => mid ? "40px" : "80px"};
  transition: 250ms ease;
  ${Button}:hover & {
    background: ${({ menu, down }) => menu || down ? color.success : color.primLight} !important;
  }
  ${Button}:active & {
    background: ${({ menu, down }) => menu || down ? color.gray : color.primDark} !important;
  }
`
export default ({ down })=> {
  const dispatch = useDispatch();
  const menu = useSelector(state => state.menu);
  const noHero = useSelector(state => state.noHero);
  const tl = gsap.timeline()

  useEffect(()=>{
    if(menu){
      tl.to('.linesMenu', .10, {
        y: index => index === 0 ? 12.5 : index === 2 ? -10 : 0,
        rotation: index => index === 0 ? 45 : index === 1 ? 0 : -45,
        width: index => index === 1 ? 0 : 30,
        ease: 'back'
      });
    } else{
      tl.to('.linesMenu', .10, {
        y: 0,
        rotation: index => index === 0 ? 0 : index === 1 ? 0 : 0,
        width: index => index === 1 ? 20 : 30,
        opacity: 1,
      });
    }
  },[menu])

  useEffect(()=> {
    tl.addLabel('fadeIn')
    .to('.linesMenu', 5, { width: index => index === 1 ? 20 : 30 }, 'fadeIn')
    .to('#menuButton', 5, { opacity: 1 }, 'fadeIn')
    .to('.linesMenu', 1, { backgroundColor: color.prim })
    return ()=> tl.ckear()
  },[])
  return(
    <Button id="menuButton" onClick={()=> dispatch({ type: ON_MENU })}>
      {
        Array(3).fill(0).map((item, index) => <Line down={down} mid={index === 1} menu={menu} className="linesMenu" index={index} key={index} />)
      }
    </Button>
  )
}
