import React, { useEffect } from 'react';
import { color, font, lorem } from '../constants';
import styled from 'styled-components';
import { gsap } from 'gsap';
import { Container, Row, Col } from 'react-bootstrap';
import reviewBackground from '../images/backgrounds/reviews.jpg';
import { ReactComponent as WinnerIcon } from '../images/iconos/mic/winner.svg';
import Title from './title';
import { ReactComponent as Quotation } from '../images/iconos/mic/quote-left.svg';
import person1 from '../images/persons/review-1.jpg';
import person2 from '../images/persons/review-2.jpg';
import { useObserver } from 'raos';

const MainCont = styled.section`
  position: relative;
  padding: 4.5rem 0;
  user-select: none;
  color: ${color.light};
  @media (min-width: 576px){

  }
`
const ReviewHeader = styled.div`
  height: 70vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  ::before{
    background-image: linear-gradient(${color.dark}, transparent);
    content: " ";
    position: absolute;
    width: 100%;
    top:0;
    left: 0;
    height: 30vh;
  }
  background-image: url(${reviewBackground});
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    @media(min-width: 576px){
      height: 100vh;
    };
`
const MediaCont = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
`
const MediaImg = styled.div`
  margin-right: 1rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: ${color.prim};
  background-image: url(${({ src }) => src});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-size: 11px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, .12),
              0px 2px 2px rgba(0, 0, 0, .12),
              0px 4px 4px rgba(0, 0, 0, .12),
              0px 8px 8px rgba(0, 0, 0, .12),
              0px 16px 16px rgba(0, 0, 0, .12);
  @media(min-width:576px){
    width: 100px;
    height: 100px;
  }
`
const MediaTitle = styled.p`
  margin: 0;
  margin-top: .5rem;
  font-size: bold;
  text-transform: capitalize;
  font-family: ${font.common};
  font-size: 11px;
  @media(min-width:576px){
    font-size: 1rem;
  }
`
const MediaSubTitle = styled(MediaTitle)`
  margin: 0;
  font-weight: normal;
  font-family: inherit;
  font-size: 11px;
  @media(min-width:576px){
    font-size: 1rem;
  }
`
const ReviewCont = styled.div`
  display: flex;
  flex-direction: column;
  opacity: 0;
  border: 10px solid ${color.prim};
  margin-bottom: 2rem;
  padding: 1rem;
  @media(min-width: 576px){
    margin-bottom: 0;
  }
`
const ReviewInfoCont = styled.div`
  padding: 2rem 0;
`
const ReviewTitle = styled.p`
  color: ${color.prim};
  font-size: 2.5rem;
  font-family: ${font.title};
  text-transform: capitalize;
`
const ReviewInfo = styled.p`
  text-align: justify;
`

const Media = ({ id, title, subTitle, img }) => (
  <MediaCont>
    <MediaImg src={img} />
    <div>
      <MediaTitle>{title}</MediaTitle>
      <MediaSubTitle>{subTitle}</MediaSubTitle>
    </div>
  </MediaCont>
);

const Review = ({ title, review, name, subInfo, img, className }) => {
  const [observe, setObserve] = useObserver();
  return(
    <ReviewCont ref={el => setObserve(el)} className={observe ? className : null}>
      <Quotation fill={color.success} width="60" height="60" />
      <ReviewInfoCont>
        <ReviewTitle>{title}</ReviewTitle>
        <ReviewInfo>
          {review}
        </ReviewInfo>
      </ReviewInfoCont>
      <Media
        title={name}
        subTitle={subInfo}
        img={img}
      />
    </ReviewCont>
  )
}


export default()=> {
  return(
    <MainCont id="reviews">
      <ReviewHeader>
        <Title>Reviews</Title>
        <WinnerIcon width="30%" height="30%" />
      </ReviewHeader>
      <Container>
        <Row>
          <Col xs={12} sm={6}>
            <Review
              className="animated fadeInLeft"
              title="Esperando tu review"
              review={lorem.lg}
              name="John doe"
              subInfo="diseños unicos"
              img={person1}
            />
          </Col>
          <Col xs={12} sm={6}>
            <Review
              className="animated fadeInRight"
              title="Esperando tu review"
              review={lorem.lg}
              name="Jenna doe"
              subInfo="diseños creativos"
              img={person2}
            />
          </Col>
        </Row>
      </Container>
    </MainCont>
  )
}
