import React, { useEffect, useRef } from 'react';
import { color, font } from '../constants';
import styled from 'styled-components';
import { gsap } from 'gsap';
import TextPlugin from 'gsap/TextPlugin';
import { useObserver } from 'raos';

const TitleCont = styled.div`
  display: flex;
  position: relative;
  z-index: 6;
  flex-direction: column;
  align-items: center;
  color: ${color.light};
  font-family: ${font.title};
  text-transform: uppercase;
  margin-bottom: 4.5rem;
  opacity: 0;
`
const Straight = styled.h2`
  font-size: 2.5rem;
  line-height: 1.5rem;
  margin: 0;
`
const Reverse = styled(Straight)`
  transform: scaleY(-1);
  opacity: 0;
`
export default ({ children })=> {
  let straight = useRef(null);
  let reverse = useRef(null);
  const [observe, setObserve] = useObserver();
  useEffect(()=> {
    gsap.registerPlugin(TextPlugin);
    const tl = gsap.timeline();
    if(observe){
      tl.set(reverse, { scaleY: -1, y: '-.3rem' })
      .set(straight, { y: '.3rem' })
      .to(straight, .5,{ text: 'oenit', repeat: 3, yoyo: true, color: color.prim })
      .to(straight, .5, { y: 0 })
      .to(reverse, .5, { opacity: 1, y: 0, opacity: 1 }, "-=.5")
      .addLabel('text')
      .to(straight, 1, { color: color.prim, x: 15, repeat: -1, repeatDelay: 1, yoyo: true, ease: 'elastic' }, 'text')
      .to(reverse, 1, { color: color.success, x: -15, repeat: -1, repeatDelay: 1, yoyo: true, ease: 'elastic' }, 'text')
    } else {
      tl.set(reverse, { scaleY: -1, y: '-.3rem', opacity: 0, x: 0, color: color.light })
      .set(straight, { text: children, color: color.light, x: 0 })
    }
  return ()=> tl.clear();
},[observe])
  return(
    <TitleCont className={observe ? 'animated fadeIn' : null} ref={el => setObserve(el)}>
      <Straight ref={el => { straight = el }}>{children}</Straight>
      <Reverse ref={el => { reverse = el }}>{children}</Reverse>
    </TitleCont>
  )
}
