import React, { useEffect, useRef, useState } from 'react';
import { color, font } from '../constants';
import styled from 'styled-components';
import { Container, Row, Col } from 'react-bootstrap';
import Button from './button';
import 'particles.js/particles';
import particlesConf from '../particlesjs-2-config.json';
import { gsap } from 'gsap';
import TextPlugin from 'gsap/TextPlugin';
import hero from '../images/backgrounds/hero.jpg';

const particlesJS = window.particlesJS;

const HeroCont = styled.div`
  position: relative;
  height: 100vh;
  background-color: ${color.dark};
  color: ${color.light};
  user-select: none;
`
const BackgroundImg = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${hero});
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
`
const ParticlesCont = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30vh;
  opacity: 0;
`
const FooterGradient = styled.div`
  position: absolute;
  left: 0;
  bottom: 0;
  background-image: linear-gradient(to top, ${color.dark}, transparent);
  height: 15vh;
  width: 100%;
`
const HeroSection = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 1;
`
const InfoCont = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  border: 15px solid ${color.prim};
  padding: 2rem;
  opacity: 0;
  @media(min-width: 576px){
    width: 70%;
    border-width: 20px;
    padding: 3rem;
  }
`
const LogoCont = styled.div`
  display: flex;
  flex-direction: column;
  color: ${color.prim};
  font-family: ${font.title};
  text-transform: uppercase;
`
const Straight = styled.h1`
  //font-size: 2rem;
  font-size: 5rem;
  line-height: 3.2rem;
  margin: 0;
  opacity: 1;
`
const Reverse = styled(Straight)`
  transform: scaleY(-1);
`
const HeroTitle = styled.h2`
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-size: 4rem;
  line-height: 3rem;
  text-transform: capitalize;
  text-align: center;
`
const Paragraph = styled.p`
  text-align: justify;
  font-size: 11px;

  @media(min-width: 576px){
    font-size: 1rem;
    width: 70%;
  }
`
const Words = styled.p`
  position: absolute;
  font-size: 2.5rem;
  font-family: ${font.title};
  text-transform: uppercase;;
  color: ${color.gray};
  @media(min-width: 576px){
    font-size: 5rem;
  }
`

export default ()=> {
  const [words, setWords] = useState(true);
  gsap.registerPlugin(TextPlugin);
  const tl = gsap.timeline();

  useEffect(()=>{
    tl.to('#words', 1, { text: 'innovador', color: color.success })
    .to('#words', 1, { text: 'creativo', color: color.prim })
    .to('#words', 1, { text: 'unico', color: color.light })
    .to('#words', 1, { text: 'precio', color: color.danger, onComplete: ()=> setWords(false) })
    .to('.fadeIn', 3, { opacity: 1 })
    .to('#heroCont', .25, { background: color.light }, '-=1')
    .to('#straight', 2,{ text: 'oenit', repeat: -1, yoyo: true, color: color.light, repeatDelay: 3 })
    .to('#reverse', 2,{ text: 'oenit', repeat: -1, yoyo: true, color: color.success, repeatDelay: 3 })
    .to('#backgroundImg', 3, { opacity: 1 }, '-=5')
    .to('#particles', .5, { opacity: 1 }, '-=1')
    particlesJS.load('particles', particlesConf);
    return () => tl.clear();
  },[])
  return(
    <HeroCont id="heroCont">
      <BackgroundImg id="backgroundImg" />
      <ParticlesCont id="particles" />
      <FooterGradient />
      <Container>
        <Row>
          {/*<Col xs={{ span: 12, order: 12 }} sm={{ span: 6, order: 0 }}>*/}
          <Col xs={12}>
            <HeroSection>
              {
                words && <Words id="words">creatividad</Words>
              }
              <InfoCont className="fadeIn">
                <LogoCont id="logo">
                  <Straight id="straight">tineo</Straight>
                  <Reverse id="reverse">tineo</Reverse>
                </LogoCont>
                <HeroTitle>web design</HeroTitle>
                <Paragraph>
                  Diseño web a medida, unicos, frescos e innovadores. Si quieres destacar en tu nicho de trabajo con presencia web, somos tu mejor opción.
                </Paragraph>
                <Button onClick={()=> window.scrollTo({ top: document.getElementById('work').offsetTop })}>Mi trabajo</Button>
              </InfoCont>
            </HeroSection>
          </Col>
        </Row>
      </Container>
    </HeroCont>
  )
}
