import React, { useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import { color, font } from '../constants';
import styled from 'styled-components';
import { gsap } from 'gsap';
import TextPlugin from 'gsap/TextPlugin';

const LogoCont = styled.a`
  display: flex;
  flex-direction: column;
  text-decoration: none !important;
  font-family: ${font.title};
  text-transform: uppercase;
  font-size: 1.5rem;
  opacity: 0;
  transition: 250ms ease;
  color: ${({ menu, down }) => menu || down ? `${color.dark} !important` : color.light};
  &:visited{
    color: ${({ menu, down }) => menu || down ? `${color.dark} !important` : color.light};
  };
  &:hover{
    color: ${({ menu, down }) => menu || down ? color.success : color.primLight} !important;
  }
  &:active{
    color: ${({ menu, down }) => menu || down ? color.gray : color.primDark} !important;
  };
`
const Straight = styled.p`
  line-height: .8rem;
  margin: 0;
  opacity: 1;
`
const Reverse = styled(Straight)`
  transform: scaleY(-1);
  opacity: 0;
`

export default ({ down })=> {
  const menu = useSelector(state => state.menu);
  let main = useRef(null);
  let straight = useRef(null);
  let reverse = useRef(null);
  gsap.registerPlugin(TextPlugin);
  const tl = gsap.timeline();

  useEffect(()=> {
    tl.set(reverse, { scaleY: -1, y: '-.3rem' })
    .addLabel('fadeIn')
    .set(straight, { y: '.3rem' })
    .to(straight, 1,{ text: 'oenit', repeat: 3, yoyo: true }, 'fadeIn')
    .to(main, 3, { opacity: 1 }, 'fadeIn')
    .to(straight, 1, { y: 0 })
    .to(reverse, 1, { opacity: 1, y: 0, opacity: 1 }, "-=1")
    .to(main, 1, { color: color.prim })
    return ()=> tl.clear();
  },[])
  return(
    <LogoCont ref={el => {main = el}} menu={menu} down={down} href="#">
      <Straight ref={el=> straight = el} >tineo</Straight>
      <Reverse ref={el=> reverse = el}>tineo</Reverse>
    </LogoCont>
  )
}

