import React, { useEffect, useState, useRef, useReducer } from 'react';
import { useSelector } from 'react-redux';
import { color, font, lorem } from '../constants';
import styled, { keyframes } from 'styled-components';
import Title from './title';
import { Container, Row, Col } from 'react-bootstrap';
import contactBackground from '../images/backgrounds/contact.jpg';
import { ReactComponent as PhoneIcon } from '../images/iconos/mic/phone-alt.svg';
import { ReactComponent as PlaneIcon } from '../images/iconos/mic/paper-plane.svg';
import { ReactComponent as MapIcon } from '../images/iconos/mic/map-marker-alt.svg';
import Button from './button';
import { gsap } from 'gsap';
import TextPlugin from 'gsap/TextPlugin';

const MainCont = styled.section`
  position: relative;
  padding: 4.5rem 0;
  position: relative;
  background-color: ${color.dark};
  background-image: url(${contactBackground});
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  user-select: none;
  color: ${color.light};
  align-items: center;
  ::before{
    background-image: linear-gradient(${color.dark}, transparent);
    content: " ";
    position: absolute;
    width: 100%;
    top:0;
    left: 0;
    height: 30vh;
  }

  @media (min-width: 576px){

  }
`
const ContactSection = styled.div`
  color: ${color.light};
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: ${({ form }) => form ? '210px' : 'auto'};
  margin-bottom: 1rem;
  @media (min-width: 576px){
    margin-bottom: 0;
    align-items: flex-start;
  }
`
const IconInfoCont = styled.div`
  display: flex;
  width: 100%;
  margin-bottom: 1rem;
`
const TextInfo = styled.p`
  margin: 0;
  margin-left: 1rem;
`
const MenuCont = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  @media(min-width: 576px){
    height: 100%;
    flex-direction: column;
    justify-content: center;
  }
`
const LinkInfo = styled.a`
  color: ${color.light} !important;
  margin-left: 1rem;
  ::before{
    content: "\\025b8";
  }
  &:active{
    color: ${color.success} !important;
  }
  @media(min-width: 576px){
    margin-left: 0;
  }
`
const ContactForm = styled.form`
  padding: 1rem;
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 5px solid ${color.prim};
  @media(min-width: 576px){
  }
`
const ContactLabel = styled.label`
  position: relative;
  display: block;
  margin-bottom: .5rem;
  padding-top: 1.5rem;
  width: 100%;
`
const ContactPlaceHolder = styled.span`
  color: #ddd;
  text-transform: uppercase;
  font-family: ${font.title};
  position: absolute;
  left: .5rem;
  bottom: 5px;
`
const ContactInput = styled.input`
  padding-left: 1rem;
  position: relative;
  z-index: 2;
  width: 100%;
  background: transparent;
  outline: none !important;
  border: none;
  border-bottom: 5px solid ${color.prim};
  color: ${color.light};
  font-size: 1rem;
  font-family: ${font.title};
`
const ContactTextarea = styled.textarea`
  padding-left: .5rem;
  width: 100%;
  border: none;
  background: ${color.prim};
  margin-bottom: 1rem;
  font-size: 1rem;
  resize: none;
  color: ${color.light};
  font-family: ${font.title};
  outline: none !important;
  ::placeholder{
    color: #ddd;
  }
`
const LoadingCont = styled.div`
  display: flex;
  flex-direction: column;
  font-family: ${font.title};
  text-transform: uppercase;
  font-size: 1.5rem;
  opacity: 1;
`
const Straight = styled.p`
  line-height: .8rem;
  margin: 0;
  opacity: 1;
`
const Reverse = styled(Straight)`
  transform: scaleY(-1);
`
const Loader = ({ request }) => {
  let straight = useRef(null);
  let reverse = useRef(null);
  useEffect(()=>{
    console.log(request);
    gsap.registerPlugin(TextPlugin);
    const tl = gsap.timeline();
    /*tl.to(straight, 1, { color: color.prim, text:"tineo", repeat: -1, yoyo: true, repeatDelay: 1 })
    .to(reverse, 1, { color: color.success, text:"tineo", repeat: -1, yoyo: true, repeatDelay: 1 }, '-=1');*/
    tl.addLabel('text')
    .to(straight, 1, { color: color.prim, text: 'tineo', x: 15, repeat: -1, repeatDelay: .5, yoyo: true, ease: 'elastic' }, 'text')
    .to(reverse, 1, { color: color.success, text: 'tineo', x: -15, repeat: -1, repeatDelay: .5, yoyo: true, ease: 'elastic' }, 'text')

  },[request])
  return(
    <LoadingCont>
      <Straight ref={el => { straight = el}}>Enviado</Straight>
      <Reverse ref={el => { reverse = el }}>Enviado</Reverse>
    </LoadingCont>
  )
}

const IconInfo = ({ Icon, children }) =>  (
  <IconInfoCont className="animated fadeIn">
    <Icon fill={color.light} width="25px" heigh="25px" />
    <TextInfo>{children}</TextInfo>
  </IconInfoCont>
)

export default ()=> {
  const nav = useSelector(state => state.staticInfo.nav);
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [request, setRequest] = useReducer((next, prev) => ({ ...next, ...prev }),{
    form: true,
    loading: false,
  });

  const sections = Object.keys(request);

  const handleRequest = (section) => {
    setRequest({
      form: sections[0] === section ? true : false,
      loading: sections[1] === section ? true : false,
    })
  }

  const onFocus = (e) => {
    const target = `#${e.target.id}`;
    if(target === '#email'){
      gsap.to(target, .5, {
        borderColor: color.primLight,
      });
      const tl = gsap.timeline({ defaults: { duration: .5} });
      tl.to(`${target}Label`,  { y: '-2rem' })
      .to(`${target}Label`, { color: color.prim });
    } else{
      gsap.to(target, .5, { background: color.primLight });
    }
  }

  const onBlur = (e) => {
    const target = `#${e.target.id}`;
    if(target === '#email' && !email){
      gsap.to(target, .5, {
        borderColor: color.prim,
      });
      const tl = gsap.timeline({ defaults: { duration: .5} });
      tl.to(`${target}Label`,  { y: 0 })
      .to(`${target}Label`, { color: '#ddd' });
    } else{
      gsap.to(target, .5, { background: color.prim });
    }
  }
  const onSubmit = async(e) => {
    e.preventDefault();
    handleRequest(sections[1])
    setTimeout(()=> handleRequest(sections[0]), 3000);
  }

  return(
    <MainCont id="contact">
      {console.log(request)}
      <Container>
        <Row>
          <Col xs={12}>
            <Title>Contacto</Title>
          </Col>
          <Col xs={12} sm={3}>
            <ContactSection>
              <IconInfo Icon={PhoneIcon}>
                (+58) 0412 - 059 - 8647
              </IconInfo>
              <IconInfo Icon={MapIcon}>
                Sucre, VE. CC hipergalerias local 11.
              </IconInfo>
              <IconInfo Icon={PlaneIcon}>
                felixtineo05@gmail.com
              </IconInfo>
            </ContactSection>
          </Col>
          <Col xs={12} sm={3}>
            <ContactSection>
              <MenuCont>
                <LinkInfo href='#'>Home</LinkInfo>
                {
                  nav.map(({ id, href, link }) => <LinkInfo key={id} href={href}>{link}</LinkInfo>)
                }
              </MenuCont>
            </ContactSection>
          </Col>
          <Col xs={12} sm={6}>
            <ContactSection form>
              {
                request.form && (
                  <ContactForm onSubmit={onSubmit}>
                    <ContactLabel>
                      <ContactPlaceHolder id="emailLabel">
                        Correo electrónico
                      </ContactPlaceHolder>
                      <ContactInput
                        id="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        onFocus={onFocus}
                        onBlur={onBlur}
                      />
                    </ContactLabel>
                    <ContactTextarea
                      id="message"
                      placeholder="Deja un mensaje"
                      value={message}
                      onChange={(e) => setMessage(e.target.value)}
                      onFocus={onFocus}
                      onBlur={onBlur}
                    />
                    <Button buttonType="submit">enviar</Button>
                  </ContactForm>
                )
              }
              {
                request.loading && <Loader request={request}/>
              }
            </ContactSection>
          </Col>
        </Row>
      </Container>
    </MainCont>
  )
}
