import React from 'react';
import Home from './pages/home';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import tineoApp from './store/reducers';
import 'animate.css';

const store = createStore(tineoApp);

function App() {
  return (
    <div>
      <Provider store={store}>
        <Home />
      </Provider>
    </div>
  );
}

export default App;
