import React, { useEffect, useRef } from 'react';
import { color, font } from '../constants';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import { ON_MENU } from '../store/actions';
import { gsap } from 'gsap';

const MainCont = styled.div`
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -110;
  overflow: hidden;
  opacity: 0;
`
const MenuCont = styled.div`
  user-select: none;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${color.light};
  position: relative;
  z-index: 120;
`
const MenuOption = styled.button`
  background: transparent;
  color: ${color.light};
  border: none;
  text-transform: uppercase;
  outline: none !important;
  font-weight: bold;
  transition: color 500ms ease;
  &:hover{
    color: ${color.dark};
  }
  &:active{
    color: ${color.gray};
  }
`
const ButtonLine = styled.div`
  height: 3px;
  width: 0;
  background-color: ${color.dark};
  transition: width 500ms ease;
  opacity: 0;
  ${MenuOption}:hover &{
    opacity: 1;
    width: 100%;
  }
  ${MenuOption}:active &{
    background-color: ${color.gray};
  }
`
export default ()=> {
  const visible = useSelector(state => state.menu);
  const nav = useSelector(state => state.staticInfo.nav);
  let contRef = useRef(null);
  const dispatch = useDispatch();
  const onScroll = (section) => {
    const el = document.querySelector(section);
    if(el){
      console.log(section);
      dispatch({ type: ON_MENU });
      window.scrollTo({ top: el.offsetTop });
    }
  }
  useEffect(()=> {
    if(visible){
      const tl = gsap.timeline();
      tl.set(contRef, { zIndex: 110 })
      .to(contRef, .25, { opacity: 1, delay: .25 });
    }else {
      gsap.to(contRef, .1, { zIndex: -110, opacity: 0 });
    }
  },[visible])

  return(
    <MainCont ref={el => { contRef = el }}>
      <MenuCont>
        {
          nav.map(({id, href, link}) => <MenuOption onClick={()=>onScroll(href)} key={id}>{link}<ButtonLine /></MenuOption> )
        }
      </MenuCont>
    </MainCont>
  )
}
