import React, { useEffect } from 'react';
import { color, font, lorem, stars } from '../constants';
import styled, { keyframes } from 'styled-components';
import Title from './title';
import { Container, Row, Col } from 'react-bootstrap';
import { useObserver } from 'raos';
import Particles from './particles';

const MainCont = styled.section`
  position: relative;
  padding: 4.5rem 0;
  position: relative;
  user-select: none;
  color: ${color.light};
  @media (min-width: 576px){

  }
`
const PricingCont = styled.div`
  height: 60vh;
  width: 100%;
  position: relative;
  margin-bottom: 2rem;
  border: 10px solid ${color.prim};
  color: ${color.light};
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  opacity: 0;
  @media(min-width: 576px){
    margin-bottom: 0;
    animation-delay: ${({ delay }) => delay};
  }
`
const PricingTitle = styled.h3`
  margin: 0;
  color: ${color.prim};
  text-transform: uppercase;
  font-size: 3rem;
`
const PricingSubtitle = styled.p`
  font-size: 1.5rem;
  text-transform: capitalize;
  font-weight: bold;
  margin: 0;
`
const Pricing = ({ title, subTitle, children, delay })=> {
  const [observe, setObserve] = useObserver();
  return(
    <PricingCont delay={delay} className={observe ? 'animated fadeInUp' : null} id={`${title}-price`} ref={el => setObserve(el)}>
      <div>
        <PricingTitle>{title}</PricingTitle>
        <PricingSubtitle>{subTitle}</PricingSubtitle>
      </div>
      <p>{children}</p>
    </PricingCont>
  )
}

export default ()=> {
  return(
    <MainCont id="pricing">
      <Particles />
      <Container>
        <Row>
          <Col xs={12}>
            <Title>Precios</Title>
          </Col>
          <Col xs={12} sm={4}>
            <Pricing delay="500ms" title="basico" subTitle="$14,99 x hora">
              {lorem.lg}
            </Pricing>
          </Col>
          <Col xs={12} sm={4}>
            <Pricing delay="1000ms" title="medio" subTitle="$29,99 x hora">
              {lorem.lg}
            </Pricing>
          </Col>
          <Col xs={12} sm={4}>
            <Pricing delay="1500ms" title="premium" subTitle="$33,99 x hora">
              {lorem.lg}
            </Pricing>
          </Col>
        </Row>
      </Container>
    </MainCont>
  )
}
