import { combineReducers } from 'redux';
import { handleBoolObject } from  '../utils';
import { ES } from '../static-info';
import {
  SET_SPANISH,
  ON_MENU,
  OUT_HERO,
  ON_SECTION,
} from './actions';

const initialState = {
  staticInfo: ES,
  menu: false,
  noHero: false,
  section: {
    home: false,
    about: false,
    service: false,
    work: false,
    pricing: false,
    contact: false,
  },
};

const staticInfo = (state = initialState.staticInfo, action) => {
  switch(action.type){
    case SET_SPANISH:
      return ES;
    default:
      return state;
  }
}

const menu = (state = initialState.menu, action) => {
  if(action.type === ON_MENU){
    return !state;
  }
  return state;
}

const noHero = (state = initialState.noHero, action) => {
  if(action.type === OUT_HERO){
    return action.payload;
  }
  return state;
}

const section = (state = initialState.section, action) => {
  const sections = Object.keys(state);
  if(action.type === ON_SECTION){
    return Object.assign({}, state, handleBoolObject(state, action.payload));
  }
  return state;
}

export default combineReducers({
  staticInfo,
  menu,
  noHero,
  section,
});
